import { __ } from '@wordpress/i18n';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextControl, ToggleControl, __experimentalNumberControl as NumberControl, PanelRow, TabPanel, Tooltip, Button, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import Background from '../../Components/Background';
import Typography from '../../Components/Typography';
import BColor from '../../Components/BColor';
import SpaceControl from '../../Components/SpaceControl';

import options from './Const/options';
const { generalStyleTabs } = options;

const Settings = ({ attributes, setAttributes }) => {
	const { prefixText, typingContents, suffixText, typeSpeed, startDelay, backSpeed, backDelay, isShuffle, isFadeOut, fadeOutDelay, isLoop, loopCount, isCursor, cursorChar, textAlign, background, padding, prefixTypo, prefixColor, typingContentsTypo, suffixTypo, suffixColor } = attributes;

	const updateContents = (index, type, val) => {
		const newContents = [...typingContents];

		newContents[index][type] = val;
		setAttributes({ typingContents: newContents });
	}

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' == tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'text-typing')}>
						<div className='helpItem'>
							<a href='https://bblockswp.com/docs/text-typing-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'text-typing')}</a>
						</div>

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/text-typing/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'text-typing')}</span>
								<span>{__('We are new and we need your help to grow!', 'text-typing')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody addRemoveItems' title={__('Texts', 'text-typing')}>
						<Title mt='0'>{__('Prefix Text:', 'text-typing')}</Title>
						<TextControl value={prefixText} onChange={val => setAttributes({ prefixText: val })} />

						<Title>{__('Typing Contents:', 'text-typing')}</Title>
						{typingContents.map((content, index) => <PanelRow key={index}>
							<TextControl value={content.text} onChange={val => updateContents(index, 'text', val)} />

							<BColor className='mt0' label='' value={content.color} onChange={val => updateContents(index, 'color', val)} />

							<Tooltip text={__('Remove this text', 'text-typing')} position='top center'>
								<Button onClick={e => {
									e.preventDefault();
									setAttributes({ typingContents: [...typingContents.slice(0, index), ...typingContents.slice(index + 1)] });
								}} ><Dashicon icon='no' /></Button>
							</Tooltip>
						</PanelRow>)}

						<div className='addItem'>
							<Button label={__('Add Typing Content', 'text-typing')} onClick={() => setAttributes({ typingContents: [...typingContents, { text: 'Text Typing', color: '#722ed1' }] })}><Dashicon icon='plus' />{__('Add Typing Content', 'text-typing')}</Button>
						</div>

						<Title>{__('Suffix Text:', 'text-typing')}</Title>
						<TextControl value={suffixText} onChange={val => setAttributes({ suffixText: val })} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Typed Settings', 'text-typing')} initialOpen={false}>
						<PanelRow>
							<Title mt='0' mb='0'>{__('Type Speed:', 'text-typing')}</Title>
							<NumberControl value={typeSpeed} onChange={val => setAttributes({ typeSpeed: parseInt(val) })} />
						</PanelRow>

						<PanelRow className='mt20'>
							<Title mt='0' mb='0'>{__('Start Delay:', 'text-typing')}</Title>
							<NumberControl value={startDelay} onChange={val => setAttributes({ startDelay: parseInt(val) })} />
						</PanelRow>

						<PanelRow className='mt20'>
							<Title mt='0' mb='0'>{__('Back Speed:', 'text-typing')}</Title>
							<NumberControl value={backSpeed} onChange={val => setAttributes({ backSpeed: parseInt(val) })} />
						</PanelRow>

						<PanelRow className='mt20'>
							<Title mt='0' mb='0'>{__('Back Delay:', 'text-typing')}</Title>
							<NumberControl value={backDelay} onChange={val => setAttributes({ backDelay: parseInt(val) })} />
						</PanelRow>

						<ToggleControl className='mt20' label={__('Enable Shuffle', 'text-typing')} checked={isShuffle} onChange={val => setAttributes({ isShuffle: val })} />

						<ToggleControl className='mt20' label={__('Enable Fade Out Effect', 'text-typing')} checked={isFadeOut} onChange={val => setAttributes({ isFadeOut: val })} />
						<small>{__('Back speed will not work if Fade Out Effect is enabled!')}</small>
						{isFadeOut && <PanelRow>
							<Title mt='0' mb='0'>{__('Fade Out Delay:', 'text-typing')}</Title>
							<NumberControl value={fadeOutDelay} onChange={val => setAttributes({ fadeOutDelay: parseInt(val) })} />
						</PanelRow>}

						<ToggleControl className='mt20' label={__('Enable Loop', 'text-typing')} checked={isLoop} onChange={val => setAttributes({ isLoop: val })} />
						{isLoop && <>
							<PanelRow>
								<Title mt='0' mb='0'>{__('Loop Count:', 'text-typing')}</Title>
								<NumberControl value={loopCount} onChange={val => setAttributes({ loopCount: parseInt(val) })} />
							</PanelRow>
							<small>{__('Loop count 0 to infinity')}</small>
						</>}

						<ToggleControl className='mt20' label={__('Enable Cursor', 'text-typing')} checked={isCursor} onChange={val => setAttributes({ isCursor: val })} />
						{isCursor && <PanelRow>
							<Title mt='0' mb='0'>{__('Cursor Character:', 'text-typing')}</Title>
							<TextControl value={cursorChar} onChange={val => setAttributes({ cursorChar: val })} />
						</PanelRow>}

						{/* <PanelRow>
							<Title mt='0' mb='0'>{__('Layout:', 'text-typing')}</Title>
							<SelectControl value={layout} onChange={val => {
							}} options={layouts} />
						</PanelRow> */}
					</PanelBody>
				</>}


				{'style' == tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Text Typing', 'text-typing')}>
						{/* <UnitControl label={__('Width:', 'text-typing')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit, perUnit, emUnit]} />
						<small>{__('Keep width 0, to auto width.', 'text-typing')}</small> */}

						<Background label={__('Background:', 'text-typing')} background={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#0000' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'text-typing')} space={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '10px', horizontal: '15px' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Text', 'text-typing')} initialOpen={false}>
						{/* Prefix */}
						<Typography label={__('Prefix Typography:', 'text-typing')} typography={prefixTypo} onChange={val => setAttributes({ prefixTypo: val })} defaults={{ fontSize: 22 }} />

						<BColor label={__('Prefix Color:', 'text-typing')} value={prefixColor} onChange={val => setAttributes({ prefixColor: val })} defaultColor='#333' />

						{/* Typing Content */}
						<Typography label={__('Typing Contents Typography:', 'text-typing')} typography={typingContentsTypo} onChange={val => setAttributes({ typingContentsTypo: val })} defaults={{ fontSize: 22, fontWeight: 600 }} />

						{/* Suffix */}
						<Typography label={__('Suffix Typography:', 'text-typing')} typography={suffixTypo} onChange={val => setAttributes({ suffixTypo: val })} defaults={{ fontSize: 22 }} />

						<BColor label={__('Suffix Color:', 'text-typing')} value={suffixColor} onChange={val => setAttributes({ suffixColor: val })} defaultColor='#333' />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;